body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  position: relative;
}

.App {
  align-items: flex-start;
  background: white;
  display: flex;
  flex-wrap: wrap;
  margin: auto;
  width: calc(2vh * 40 + 2px);
}

.App .block {
  border: 1px solid #000;
  box-sizing: border-box;
  font-size: 2vh;
  font-weight: bold;
  height: calc(2vh + 2px);
  line-height: calc(2vh + 2px);
  text-align: center;
  width: 2vh;
}

.block.up {
  border-top: 1px solid blue
}

.block.right {
  border-right: 1px solid blue
}

.block.down {
  border-bottom: 1px solid blue
}

.block.left {
  border-left: 1px solid blue
}

.logo {
  -webkit-animation: logo-spin infinite 20s linear;
          animation: logo-spin infinite 20s linear;
}

.count {
  -webkit-animation: logo-spin infinite 1s linear;
          animation: logo-spin infinite 1s linear;
}

@-webkit-keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

